figure {
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0; }

$baseColor: #2D2D2D;

#left input {
    width: 60px; }

.ui-button-text {
    font-family: "Roboto";
    font-weight: 700;
    font-size: 10px;
    color: $baseColor; }

.ui-title-text {
    font-family: "Roboto Condensed";
    font-weight: 700;
    font-size: 18px;
    color: $baseColor; }

.ui-info-text {
    font-family: "Roboto Condensed";
    font-weight: normal;
    font-size: 12px;
    color: #B8B8B8; }

.fl {
    float: left; }

.fr {
    float: right; }

// BEGIN
html, body {
    width: 100%;
    height: 100%; }

body {
    font-family: "Roboto";
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 0; }

// LAYOUT: GLOBAL HEADER
.frame {
    box-sizing: border-box;
    width: 100%; }

#global-header, #global-footer {
    position: fixed; }

#global-header {
    top: 0px;
    padding: 30px 30px 0 30px;
    z-index: 3; }

#option {
    float: right;
    text-transform: uppercase;

    a {
        float: left;
        @extend .ui-button-text;
        cursor: pointer;
        margin-left: 15px;

        &:first-child {
            margin-left: 0; } } }

// GLITCH TEXT
#btn-information {
    width: 68px;
    overflow: visible; }

@mixin glitchTransform( $tx, $ty, $rx, $ry ) {
    transform: translateX(#{$tx}px) translateY(#{$ty}px) rotateX(#{$rx}deg) rotateY(#{$ry}deg); }

.glitch {
    position: relative;
    perspective: 300px;

    p {
        margin: 0; } }

.glitch-original {
    display: inline-block;
    position: relative;
    z-index: 3; }

.glitch-cyan, .glitch-magenta {
    position: absolute;
    opacity: 0;
    left: 0;
    top: 0; }

.glitch {
    &.glitch-right {
        .glitch-cyan, .glitch-magenta {
            left: auto;
            right: 0; } } }

.glitch-cyan {
    z-index: 1;
    color: #00FFFF;
    @include glitchTransform(4,0,10,-2); }

.glitch-magenta {
    z-index: 2;
    color: #FF0000;
    @include glitchTransform(-4,0,-10,2); }

// Glitch Effect CSS Anim
.glitch:hover {
    .glitch-original {
        animation: 0.8s ease-out GlitchOriginal; }
    .glitch-cyan {
        animation: 0.8s ease-out GlitchCyan; }
    .glitch-magenta {
        animation: 0.8s ease-out GlitchMagenta; } }

@keyframes GlitchCyan {
    0% {
        opacity: 0.8; }

    80% {
        opacity: 0.8;
        @include glitchTransform(-2,0,-10,2); }

    99% {
        opacity: 0.2;
        @include glitchTransform(-1, 0, 0, 0); } }


@keyframes GlitchMagenta {
    0% {
        opacity: 0.8; }

    80% {
        opacity: 0.8;
        @include glitchTransform(2,0,10,-2); }

    99% {
        opacity: 0.2;
        @include glitchTransform(1, 0, 0, 0); } }


@keyframes GlitchOriginal {
    0% {
        opacity: 0.5; }

    99% {
        opacity: 0.8; } }

#product-name.glitch p {
    right: 0; }

.radio {
    float: right;
    width: 7px;
    height: 7px;
    margin-top: 3px;
    margin-left: 6px;
    border-radius: 50%; }

#option a[data-status="on"] > .radio {
    background: #2D2D2D;
    transition: background .6s ease-out; }

#option a[data-status="off"] > .radio {
    background: #C9C9C9;
    transition: background .6s ease-out; }

#global-footer {
    height: 90px;
    bottom: 0px;
    padding: 0 30px;
    z-index: 2; }

#main {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;

    // for vertical align to #stage
    display: flex;
    justify-content: center;
    flex-direction: column; }

#stage {
    display: block;
    margin: 0 auto; }

#left {
    float: left; }

// bottom right side
#right {
    float: right; }

#product {
    height: 90px;
    clear: both;
    h1,h2 {
        text-align: right;
        text-transform: uppercase; } }

#product-headers {
    margin-right: 5px; }

#product-name {
    @extend .ui-title-text;
    line-height: 14px;
    margin: 0 0 4px 0; }

#product-info, #product-stat {
    line-height: 1em;
    margin: 0; }

#thumbnails {
    float: right;
    width: 90px;
    height: 90px;
    overflow: hidden;
    background: #2D2D2D;
    position: relative; }

#thumbnail-track {
    position: absolute;
    width: 90px;
    top: 0px;
    left: 0px;

    img {
        margin: 0;
        padding: 0;
        width: 90px;
        height: 90px;
        display: block; } }

// CLOCK
#clock {
    font-size: 24px;
    height: 38px; }

#milli-clock, #info-total, #info-name {
    display: none; }

#author, #project-title {
    margin: 0; }

#author {
    line-height: 15px;
    margin-bottom: 3px;

    img {
        width: 88px;
        height: 15px; } }

#project-title {
    line-height: 23px;

    img {
        width: 168px;
        height: 23px; } }

// CONSOLE
#console {
    display: none;
    z-index: 10;
    position: absolute;
    top: 0px;
    background: #0D0D0D;
    color: white;
    width: 100%;
    height: 20px;
    line-height: 20px;

    input {
        border: none;
        margin-right: 2px; } }

// #stage
#dev-thumb {
    position: absolute;
    bottom: 0px;
    z-index: 150;
    margin: 0;
    padding: 0;
    list-style: none;
    height: 30px;

    li {
        display: inline-block;
        width: 30px;
        height: 30px;

        img {
            filter: grayscale( 100% );
            width: 100%; } } }

// THREE.js stage
#three-stage {
    width: 100%;
    height: 100%;

    canvas {
        width: 100%;
        height: 100%; } }

#fixed {
    position: fixed; }

#test-list {
    position: absolute;
    top: 0; }
